@import '../../styles/fonts';

.cell {
	width: 40px;
	height: 40px;
	display: flex;
	justify-content: center;
	align-items: center;
	border: solid 1px #444;
	border-bottom: solid 1px #222;
	border-left: solid 1px #222;
	cursor: pointer;
	user-select: none;
	@include font-regular(26px);
	border-radius: 4px;
}
